html {
  box-sizing: border-box;
}

body {
  color: #000;
  min-height: 100vh;
  background-color: #000;
  font-family: Roboto, sans-serif;
  font-size: 24px;
  font-weight: 400;
  line-height: 1.16667;
}

*, :after, :before {
  box-sizing: inherit;
}

ul, ol {
  padding: 0;
}

body, h1, h2, h3, h4, h5, h6, p, ul, ol, li, figure, figcaption, blockquote, dl, dd {
  margin: 0;
}

ul {
  list-style: none;
}

img {
  max-width: 100%;
  display: block;
}

input, button, textarea, select {
  font: inherit;
  border: none;
}

input::placeholder {
  padding: 0;
}

a {
  color: inherit;
  cursor: pointer;
  text-decoration: none;
}

button {
  cursor: pointer;
  background-color: #0000;
  padding: 0;
}

.countdown {
  text-align: center;
  min-height: 100vh;
  justify-content: center;
  align-items: center;
  padding: 0 15px;
  display: flex;
}

.countdown__title {
  color: #fff;
  margin-bottom: 30px;
}

.countdown__form {
  max-width: 500px;
  width: 100%;
  height: 220px;
}

.countdown__action {
  justify-content: center;
  margin-bottom: 20px;
  display: flex;
}

.countdown__label {
  max-width: 380px;
  width: 100%;
}

.countdown__input {
  width: 100%;
  height: 100%;
  background-color: #fff;
  border-right: 2px solid #000;
  border-radius: 5px 0 0 5px;
  padding: 10px;
}

input::-webkit-calendar-picker-indicator {
  cursor: pointer;
}

.countdown__btn {
  background-color: #fff;
  border-radius: 0 5px 5px 0;
  padding: 10px 20px;
  transition: color .3s, background-color .3s;
}

.countdown__btn:hover {
  color: #fff;
  background-color: gray;
}

.countdown__time {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  transition: color 1s;
  display: none;
}

.countdown__time span {
  display: block;
}

.countdown__error {
  justify-content: center;
  transition: color 1s;
  display: none;
}

.countdown__set-date {
  color: #fff;
  justify-content: center;
  transition: color 1s;
}

@media (max-width: 767.98px) {
  body {
    font-size: 16px;
  }
}

